import { getHostName } from "../BrandContext/BrandContextProvider";

type DataLayer = {
    event: string;
    category: string;
    action: string;
    label: string;
};

const saveGa = (dataLayer: DataLayer) => {
    if (!getHostName().hostname.includes("localhost")) {
        if (typeof window !== "undefined") {
            (window as any).dataLayer.push({ ...dataLayer });
        }
    }
};

const SaveAluguelCasa = (): void => {
    const dataLayer: DataLayer = {
        event: "menu-principal",
        category: "menu",
        action: "principal-mais",
        label: "aluguel de casas",
    };
    saveGa(dataLayer);
};

const GAEvents = { SaveAluguelCasa };

export default GAEvents;
